import React, { Component } from 'react';
import Calendar from 'react_google_calendar'
import 'react-big-calendar/lib/css/react-big-calendar.css';

//Google Calendar
const YOUR_GOOGLE_API_KEY = 'AIzaSyC6cXkF_cZugxpk1jevx442dteiFmm7T-k';

const calendar_configuration = {
  api_key: YOUR_GOOGLE_API_KEY,
  calendars: [
    {
      name: 'BBBAutoSchoolCalendar', // whatever you want to name it
      url: 'https://calendar.google.com/calendar/embed?src=bbbautoschool%40gmail.com&ctz=America%2FNew_York' // your calendar URL
    }
  ],
  dailyRecurrence: 700,
  weeklyRecurrence: 500,
  monthlyRecurrence: 20
}

class Schedule extends Component {
  constructor(props) {
    super(props)
      this.state = {
        events: []
      }
  }
  render() {
    return (     
      <div style={{ height: 700 }}>
          <Calendar
          events={this.state.events}
          config={calendar_configuration} />
        </div>
    );
  }
}

export default Schedule;
