import React, { Component } from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Jumbotron from '../components/Jumbotron';
import JumboTronContact from '../res/behind_mirror.jpg';

import './css/Contact.css';

class Contact extends Component {
  render() {
    return (
      <div>
        <div>
          <Jumbotron imageSource={JumboTronContact}/>
          <br />
          <div className="allCards">
            <Grid container spacing={24}>
            <Grid item xs={3}>
              <Card>
                  <CardContent>
                    <Typography color="textSecondary" gutterBottom>
                      Visit our Office!
                    </Typography>
                    <a href="https://goo.gl/maps/zKYX8P3SE6q" target="_blank" rel="noopener noreferrer">
                      <Typography variant="h5" component="h2">
                      <i className="fas fa-map-marked-alt fa-2x"></i>
                      </Typography>
                      <Typography component="p">
                        <br />
                        106 Weir St
                        <br />
                        Taunton, MA 02780
                      </Typography>
                    </a>
                  </CardContent>
                </Card>
            </Grid>
            <Grid item xs={3}>
            <Card>
                <CardContent>
                  <Typography color="textSecondary" gutterBottom>
                    Give us a Call!
                  </Typography>
                  <a href="tel:1-508-880-3333">
                    <Typography variant="h5" component="h2">
                    <i className="fas fa-phone fa-2x"></i>
                    </Typography>
                    <Typography component="p">
                        <br />
                        <br />  
                        (508) 880-3333
                        <br />
                    </Typography>
                  </a>
                </CardContent>
              </Card>
            
            </Grid>
            <Grid item xs={3}>
            <Card>
                <CardContent>
                  <Typography color="textSecondary" gutterBottom>
                    Looking for our Fax?
                  </Typography>
                  <Typography variant="h5" component="h2">
                  <i className="fas fa-fax fa-2x"></i>
                  </Typography>
                  <Typography component="p">
                      <br />
                      <br />
                      (508) 880-3332
                      <br />
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={3}>
            <Card>
                <CardContent>
                  <Typography color="textSecondary" gutterBottom>
                    Send us an email!
                  </Typography>
                  
                  <a href="mailto:bbbautoschool@gmail.com">
                    <Typography variant="h5" component="h2">
                    <i className="fas fa-envelope-open fa-2x"></i>
                    </Typography>
                    <Typography component="p">  
                      <br />
                      <br />
                      bbbautoschool@gmail.com
                      <br />
                    </Typography>
                  </a>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </div>
      </div>
      <hr width="0"/>
      <iframe 
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2969.7225843726255!2d-71.09381878455827!3d41.89882287922056!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89e4f354a1b3a913%3A0x695e7bd7495d24c7!2s106+Weir+St%2C+Taunton%2C+MA+02780!5e0!3m2!1sen!2sus!4v1552270654164" 
        width="700"
        title="Google Maps - BBB Auto"
        height= "500"
        frameborder="0"  
        allowfullscreen />
    </div>
    );
  }
}

export default Contact;
