import React, { Component } from 'react';
import JumboTronServices from '../res/girl_on_road.jpg';
import Jumbotron from '../components/Jumbotron';

import './css/Services.css';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';

import LocalCarWash from '@material-ui/icons/LocalCarWash';
import Forward5 from '@material-ui/icons/Forward5';
import Person from '@material-ui/icons/Person';
import FastForward from '@material-ui/icons/FastForward';
import Create from '@material-ui/icons/Create';

class Services extends Component {
  constructor() {
    super();
    this.state = {
      componentToShow: ''
    }
    this.handleClick = this.handleClick.bind(this);
    this.simulateClick = this.simulateClick.bind(this);
  }
  

  handleClick(args) {
    this.setState({
      componentToShow: args
    })
  }
  simulateClick(e) {
    //Simulate a click on Driver's Ed so page doesn't load empty
    if(e != null){
      e.props.onClick();
    }
  }
  

  render() {
    return (
      <div>
        <Jumbotron imageSource={JumboTronServices} />
        <h2>Services</h2>
        <div className="container">
          <div className="leftContainer">
            <List component="nav" className="List">

              <ListItem button ref={this.simulateClick} onClick={() => this.handleClick("Driver's ED Program")}>
                <ListItemIcon> <LocalCarWash /> </ListItemIcon>
                <ListItemText primary="Driver's ED Program" />
              </ListItem>

              <ListItem button onClick={() => this.handleClick("1 Week Accelerated Class")}>
                <ListItemIcon> <FastForward /> </ListItemIcon>
                <ListItemText primary="1 Week Class" />
              </ListItem>

              <ListItem button onClick={() => this.handleClick("5 Week Class")}>
                <ListItemIcon> <Forward5 /> </ListItemIcon>
                <ListItemText primary="5 week class" />
              </ListItem>

              <ListItem button onClick={() => this.handleClick("Private Lesson")}>
                <ListItemIcon> <Person /> </ListItemIcon>
                <ListItemText primary="Private Lesson" />
              </ListItem>

              <ListItem button onClick={() => this.handleClick("Road Test")}>
                <ListItemIcon> <Create /> </ListItemIcon>
                <ListItemText primary="Road Test" />
              </ListItem>

            </List>
          </div>
          <div className="rightContainer">
            <Frame
              componentToShow={this.state.componentToShow} />
          </div>
        </div>
      </div>
    );
  }
}

class Frame extends Component {
  render() {
    return (
      <div>
        <h3>{this.props.componentToShow}</h3>
      </div>
    );
  }
}

export default Services;
